async function Read_markdown(adds: string[]) {
  const ret: string[] = [];
  
  for (const add of adds) {
    await fetch(add)
      .then(res => res.text())
      .then(text => {
        ret.push(text);
      })
      .catch(error => {
        console.error(`Error fetching ${add}: ${error}`);
      });
  }
  
  console.log(ret);
  return ret;
}

